.account-available {
  height: 32px;
  padding: 8px 16px;
  font-size: 14px;
  line-height: 16px;
  background-color: rgb(243, 243, 247);
  color: rgb(92, 99, 112);
  outline: none;
  border: none;
  border-radius: 9999px;
  text-align: center;
  font-weight: 500;
  &:hover {
    color: black;
    cursor: pointer;
  }
}
