.cartItem {
  display: flex;
  flex-direction: column;
  padding: 10px 20px;

  margin-bottom: 15px;
  border: 1px solid gray;
  border-radius: 25px;
  position: relative;
  &__img {
    width: 60px;
    margin-right: 10px;
  }
  &__name {
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: rgb(0, 0, 0);
    margin: 0px 0px 4px;
  }
  &__options {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: rgb(92, 99, 112);
    margin: 0px;
    white-space: pre-wrap;
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    -webkit-box-align: center;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    color: rgb(0, 0, 0);
  }
  &__buttonGroup {
    display: flex;
    align-items: center;
    position: relative;
    width: 96px;
    height: 32px;
    background-color: rgb(243, 243, 247);
    box-sizing: border-box;
    border-radius: 9999px;
    -webkit-box-pack: justify;
    justify-content: space-between;
  }
  &__svg {
    position: absolute;
    top: 10px;
    right: 10px;
    &:hover {
      cursor: pointer;
    }
    &:hover svg path {
      fill: black;
    }
    & svg {
      display: block;
      width: 25px;
      height: 25px;
      & path {
        fill: gray;
      }
    }
  }
}
