.ingItem {
  display: flex;
  align-items: center;
  margin-right: 5px;
  position: relative;
  &__true:before {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0px;
    left: 0px;
    right: 16px;
    border-bottom: 1px dashed rgb(92, 99, 112);
  }
  &.ingItem__false {
    .ingItem__title {
      text-decoration: line-through;
    }
  }
  &:hover {
    cursor: pointer;
  }
  &__title {
    font-size: 12px;
    line-height: 15px;

    margin-right: 3px;
  }
}
