.bodyPayment {
  margin: 20px 0 20px 0;
  & .delivery,
  & .payment {
    display: flex;
    justify-content: space-between;
  }
  & .delivery-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    padding: 20px 10px 20px 10px;
    border: 1px solid black;
    border-radius: 20px;
    margin: 20px 0 20px 0;
    & .deliveryInput {
      display: flex;
      align-items: center;
      & input {
        margin-left: 10px;
        width: 100%;
        font-size: 18px;
        border-radius: 5px;
        outline: none;
        &:active,
        &:focus-visible {
          outline: none;
        }
      }
    }
  }
  & .pickup-content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    padding: 20px 10px 20px 10px;
    border: 1px solid gray;
    border-radius: 20px;
    margin: 20px 0 20px 0;
    &__item {
      border: 1px solid orange;
      padding: 5px;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      &:hover {
        cursor: pointer;
      }
    }
    &__item.selected {
      background-color: orange;
    }
  }
}
