.promocode-input {
  padding: 16px 124px 16px 24px;
  flex: 0 0 auto;
  font-weight: 500;
  font-size: 19px;
  line-height: 20px;
  width: 100%;
  box-sizing: border-box;
  border: 0;
  background: rgb(243, 243, 247);
  border-radius: 10px;
  &.promoTrue {
    padding: 16px 124px 16px 60px;
    color: #008739;
  }
  &:disabled {
    cursor: no-drop;
  }
  &:active,
  &:focus-visible {
    outline: none;
  }
  &:focus {
    border: none;
  }
}
.promocode-div {
  border-radius: 10px;
  border: 1px solid gray;
  position: relative;
  & svg {
    position: absolute;
    display: flex;
    height: 100%;
    align-items: center;
    padding-left: 24px;
  }
}
.promocode-button {
  position: absolute;
  top: 0;
  right: 20px;
  align-items: center;
  display: flex;
  height: 100%;
  & button {
    font-size: 16px;
    color: rgb(177, 120, 14);
    line-height: 1;
    &:hover {
      cursor: pointer;
    }
  }
}
.promocode-errorBlock {
  position: absolute;
  bottom: 1px;
  font-size: 11px;
  left: 24px;
  color: red;
}
