.shop-item {
  display: flex;
  flex-flow: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  cursor: pointer;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 20px;
  &:hover {
    box-shadow: 0px 1px 14px 2px rgb(34, 60, 80, 0.2);
  }
  & .img {
    position: relative;
    & .feature {
      position: absolute;
      top: 35px;
      right: 10px;
      font-size: 22px;
      border-radius: 50px;
      padding: 5px 10px 5px 10px;
      color: white;
    }
  }
  & .img img {
    box-sizing: border-box;
    width: 250px;
    height: 250px;
  }
  & .title {
    margin: 0px 0px 8px 0;
    color: rgb(0, 0, 0);
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
  }
  & .descriptions {
    font-size: 14px;
    font-family: "Roboto";
    font-weight: 400;
    color: #4e4e4e;
    height: 51px;
    min-height: 51px;
  }
  & .control-wrap {
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    & .price {
      font-weight: 500;
      line-height: 42px;
      padding-right: 4px;
      font-size: 22px;
      color: rgb(0, 0, 0);
    }
  }
}
