.menuItem {
  display: inline-block;
  vertical-align: middle;
  margin-left: 20px;
  &:first-child {
    margin-left: 0px;
  }
  & a {
    text-decoration: none;
    color: black;
    padding: 22px 0px;
    display: block;
    font-size: 14px;
    &:hover {
      color: rgb(255, 105, 0);
    }
  }
}
