.sides {
  margin-top: 15px;
  &-wrap {
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    max-height: 340px;
    margin-top: 10px;
    padding-left: 8px;
  }
  & .side {
    cursor: pointer;
    box-sizing: border-box;
    touch-action: manipulation;
    position: relative;
    height: 166px;
    width: calc(33.3333% - 8px);
    box-shadow: rgba(6, 5, 50, 0.12) 0px 4px 20px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    font: inherit;
    margin: 0px 8px 8px 0px;
    padding: 8px;
    background: rgb(255, 255, 255);
    border-width: 1px;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    border-image: initial;
    border-radius: 12px;
    flex-flow: column;
    transition: box-shadow 150ms ease-out 0s, border;
    justify-content: space-between;
    & svg {
      position: absolute;
      right: 5px;
      top: 5px;
      & path {
        fill: rgb(255, 105, 0);
      }
    }
    &.active {
      box-shadow: rgb(6, 5, 50, 0.12) 0px 0px 0px;
      border: 1px solid rgb(255, 105, 0);
    }
    & .side-wrap {
      display: flex;
      flex-direction: column;
      & img {
        width: 100%;
      }
      & .title {
        font-size: 11px;
        line-height: 16px;
        height: 32px;
        flex: 1 0 auto;
        margin: 0px;
      }
    }
    & .price {
      font-weight: 500;
    }
  }
}
