.orderedItem {
  background: rgb(255, 255, 255);
  transition: box-shadow 150ms ease-out 0s;
  box-shadow: rgb(6, 5, 50, 0.15) 0px 4px 22px -6px;
  border-radius: 12px;
  display: flex;
  flex: 0 0 auto;
  cursor: pointer;
  width: 260px;
  padding: 16px;
  margin-right: 24px;
  box-sizing: border-box;
  &:hover {
    transform: scale(1.1);
  }
  & img {
    width: 80px;
    height: 80px;
    flex: 0 0 auto;
    pointer-events: none;
  }
  & .information {
    display: flex;
    flex-direction: column;
    justify-content: center;
    & div {
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      max-height: 40px;
    }
  }
}
