.nav {
  width: 100%;
  position: sticky;
  top: 0px;
  background-color: white;
  z-index: 10;
  & .wrapp {
    position: relative;
    width: 1280px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &.scrolled::before {
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    box-shadow: rgba(6, 5, 49, 0.1) 0px 4px 30px;
  }
}
