.tabs-item {
  width: 48%;
  height: 100px;
  border-radius: 20px;
  border: 1px solid gray;
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover {
    cursor: pointer;
    background-color: rgb(216, 211, 211);
  }
  &.active {
    background-color: orange;
  }
}
