.oftenOrdered {
  width: 100%;
  height: 200px;
  position: relative;
  user-select: none;
  transform: translate3d(0px, 0px, 0px);
  backface-visibility: hidden;
  padding: 0px calc((100% - 1280px) / 2);
  overflow: hidden;
  transition: height 200ms ease-out 0s, opacity;
  box-sizing: border-box;
}
