$width: 200px;
$height: 238px;

.promotionItem {
  position: relative;
  height: $height;
  display: flex;
  justify-content: center;
  align-items: center;
  width: $width;
  margin-right: 16px;
  &:hover {
    cursor: pointer;
  }
  & .content {
    width: $width - 10px;
    height: $height - 10px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    // background-image: url("../../../../images/promotionItemBg.jpg");
    border-radius: 25px;
  }
  & .border {
    position: absolute;
    border: 2px solid #ff6900;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 25px;
    box-sizing: border-box;
  }
}
