.account-empty {
  height: 50px;
  border-radius: 20px;
  padding: 0px 5px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  &:hover {
    border: 2px solid rgb(255, 105, 0);
    cursor: pointer;
    background-color: rgb(243, 243, 247);
  }
}
