.searchInput {
  position: relative;
  width: 350px;
  display: inline-block;
  padding-left: 40px;
  height: 40px;
  border-radius: 8px;
  border: 1px solid rgba(92, 99, 112, 0.32);
  padding: 0px 15px 0 40px;
  color: rgb(0, 0, 0);
  font-size: 16px;
  line-height: 40px;
  box-sizing: border-box;
  font-weight: 500;
  &:active,
  &:focus-visible {
    outline: none;
    border: 1px solid #ff6900;
  }
}
.searchInput-wrap {
  position: relative;
  & svg {
    position: absolute;
    left: 10px;
    top: calc(50% - 12px);
  }
}
