.switchItem {
  display: block;
  flex: 1 1 0%;
  position: relative;
  cursor: pointer;
  user-select: none;
  border-radius: 9999px;
  color: rgb(0, 0, 0);
  font-size: 12px;
  line-height: 26px;
  font-weight: normal;
  text-align: center;
  height: 32px;
  & label {
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
    &:hover {
      cursor: pointer;
    }
  }
  & input {
    position: absolute;
    display: none;
  }
  & input:checked + label {
    display: block;
    border: 3px solid rgb(243, 243, 247);
    box-sizing: border-box;
    border-radius: 9999px;
    background-color: white;
    box-shadow: rgb(6, 5, 50, 0.19) 0px 6px 20px;
  }
}
