.promotion {
  display: flex;
  overflow: hidden;
  position: relative;
  padding-top: 12px;
  &__body {
    display: flex;
    overflow: visible;
    &.transition {
      transition: all 0.5s ease-out;
    }
  }
}
.promotion--wrap {
  width: 1280px;
  margin: auto;
  position: relative;
}
