.location {
  display: flex;
  &__title {
    white-space: pre-wrap;
    color: rgb(0, 0, 0);
    font-size: 18px;
    font-weight: 500;
    margin-right: 5px;
  }
  &__city {
    white-space: pre-wrap;
    color: rgb(255, 105, 0);
    font-size: 18px;
    font-weight: 500;
    &:hover {
      cursor: pointer;
    }
  }
}
