.control {
  position: absolute;
  z-index: 2;
  top: 0;
  display: flex;
  height: 100%;
  align-items: center;
  &.control-left {
    left: -40px;
    & .control-icon img {
      transform: rotate(180deg);
    }
  }
  &.control-right {
    right: -40px;
  }
  & .control-icon img {
    width: 20px;
    height: 35px;
  }
  & .control-icon {
    display: flex;
    &:hover {
      cursor: pointer;
    }
  }
}
